import React, { Component } from "react";
import Results from "../Results/Results";
import classes from "./Page.module.css";
import {Link} from 'react-router-dom';
class Page extends Component {
  render() {
    let pathArray = this.props.location.pathname.split('/')
    
    return (
  
        <div className={classes.container}>
          <div className={classes.title}>
            Here's a list of news within the past 6 months for games you've played recently, as well as
            games you've put more than {pathArray[2]} hours in. If the list is small, check
            your steam privacy settings!
          </div>
           <Link style={{'textDecoration': 'none'}}  className={classes.link} to={`/`}><div className={classes.button}>New Search</div> </Link>
          <Results steamID={pathArray[1]} value={pathArray[2]} />
        </div>

    );
  }
}

export default Page;
