import React, { Component } from "react";
import { Link } from "react-router-dom";
import classes from "./Landing.module.css";
import Slider from "react-rangeslider";
import 'react-rangeslider/lib/index.css'
var classNames = require('classnames');
//not played in 2 weeks 76561198018232960 3dayrespawn
//has played 2 games in 2 weeks kast master
//plays lots of games
class Landing extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inputValue: "",
      value: 10,
      disabled: true
    };
  }
  componentDidMount(){
    fetch(`https://ancient-dusk-43980.herokuapp.com/440/news`)
    .then(res => res.json())
    .then(data => {
      console.log(data)
  })
}
  handleChangeStart = () => {
    console.log("Change event started");
  };

  handleChange = value => {
    this.setState({
      value: value
    });
  };

  handleChangeComplete = () => {
    console.log("Change event completed");
  };
  updateInputValue(evt) {

   
    if(evt.target.value.length === 17) {
      this.setState({
        disabled: false
      }) 
      console.log(evt.target.value.length)
    console.log(this.state.disabled)
    } else {
      this.setState({
        disabled: true
      })
      console.log(evt.target.value.length)
    console.log(this.state.disabled)
    }
    this.setState({
      inputValue: evt.target.value
    });
  }
  render() {
    const { value } = this.state;
    return (
      <div className={classes.container}>
        <div className={classes.title}>
          <div className={classes.header}>Steam News Generator</div>
          <div className={classes.subheader}>
            Recent news for your most recent and favorite games!
          </div>
        </div>
        <div className={classes.main}>
          <input
            className={classes.input}
            type="search"
            value={this.state.inputValue}
            onChange={evt => this.updateInputValue(evt)}
            placeholder="Enter steam ID"
          />
         
          <div className='slider'>
        <Slider
          min={0}
          max={100}
          value={value}
          onChangeStart={this.handleChangeStart}
          onChange={this.handleChange}
          onChangeComplete={this.handleChangeComplete}
        />
      </div>
      <Link
            style={{ textDecoration: "none" }}
            className={classes.link}
            to={`/${this.state.inputValue}/${this.state.value}`}
          >
            <button disabled={this.state.disabled} className={classes.button}>Search with games above {value} hours</button>{" "}
          </Link>
        </div>
      </div>
    );
  }
}

export default Landing;
